import { Button, Sticky } from "@taroify/core"
import { View } from "@tarojs/components"
import { useRef } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicSticky() {
  return (
    <Block title="">
      <Sticky>
        <Button color="primary"></Button>
      </Sticky>
    </Block>
  )
}

function OffsetSticky() {
  return (
    <Block title="" className="sticky-demo__offset-sticky">
      <Sticky offsetTop={50}>
        <Button color="primary"></Button>
      </Sticky>
    </Block>
  )
}

function StickyWithContainer() {
  const container = useRef()
  return (
    <Block title="">
      <View className="sticky-demo__container-sticky" ref={container}>
        <Sticky container={container}>
          <Button color="warning"></Button>
        </Sticky>
      </View>
    </Block>
  )
}

function BottomSticky() {
  return (
    <Block title="" className="sticky-demo__bottom-sticky">
      <View className="sticky-demo__bottom-sticky__padding" />
      <Sticky position="bottom" offsetBottom={50}>
        <Button color="primary"></Button>
      </Sticky>
      <View className="sticky-demo__bottom-sticky__padding" />
    </Block>
  )
}

export default function StickyDemo() {
  return (
    <Page title="Sticky " className="sticky-demo">
      <BasicSticky />
      <OffsetSticky />
      <StickyWithContainer />
      <BottomSticky />
    </Page>
  )
}
